// src/lib/save.ts

import type { Save } from "./types/save";

export function save_get(version: string): Save {
  const local_storage_save_value = localStorage.getItem('jdefense');
  if (local_storage_save_value === null) {
    const new_save = save_init(version);
    localStorage.setItem('jdefense', JSON.stringify(new_save));
    return new_save;
  } else {
    const current_save = JSON.parse(local_storage_save_value) as Save;
    if (current_save.version !== version) {
      const new_save = save_init(version);
      localStorage.setItem('jdefense', JSON.stringify(new_save));
      return new_save;
    } else {
      return current_save;
    }
  }
}

function save_init(version: string): Save {
  return {
    version,
    mission: "m001"
  }
}